@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Marck+Script&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Nunito:ital,wght@0,200..1000;1,200..1000&family=Oswald:wght@200..700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Sora:wght@100..800&family=Tenor+Sans&display=swap');

body {
  background-color: rgb(58, 69, 75);
  color: rgb(255, 202, 40);
  font-family: "Montserrat", sans-serif;
}

.App {
  margin-top: 15px;
  margin-left: 75px;
  font-size: 22px;
  width: 85wh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
}

.logo {
  color: rgba(38, 50, 56, 0.266);
  font-size: 400px;
  position: fixed;
  bottom: -300px;
  right: 0px;
  cursor: default;
}

.header h1 {
  cursor: default;
  font-size: 44px;
}

.header h1 span {
  color: #fff;
}

.question-element {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  cursor: default;
}

.hide {
  display: none;
}

.answers-buttons {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 25px;
}

.answers-buttons button {
  text-align: start;
  padding-left: 15px;
  height: 60px;
  width: 450px;
  border: 0px;
  border-radius: 5px;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  color: rgb(255, 255, 255);
  background-color: rgb(38, 50, 56);
  &:hover {
    cursor: pointer;
    opacity: 0.7;
  }
}

@media(max-width: 1200px) {
  .App {
    /* margin-top: 15px; */
    margin-left: calc(15px + (75 - 15) * ((100vw - 320px) / (1200 - 320)));
    font-size: 22px;
    width: calc(95vw + (85 - 95) * ((100vw - 320px) / (1200 - 320)));
  }
  
  .logo {
    display: none;
  }

  .header h1 {
    font-size: calc(25px + (44 - 25) * ((100vw - 320px) / (1200 - 320)));;
  }

  .answers-buttons {
    justify-content: center;
    align-items: center;
  }

  .answers-buttons button {
    padding-left: 15px;
    height: 60px;
    width: 95%;

  }
}

